import React, {useState, useEffect} from "react"
import AdminHeader from "./layouts/AdminHeader"
import AdminSideBar from "./layouts/AdminSideBar"
import AdminFooter from "./layouts/AdminFooter"
import {Link} from "react-router-dom"
import {db} from "../firebase"
import {collection, getDocs, doc, deleteDoc} from "firebase/firestore"

export default function Order() {
	var counter = 1
	const [orders, setOrders] = useState([])

	const ordersCollectionRef = collection(db, "zb_order")
	const getOrders = async () => {
		const data = await getDocs(ordersCollectionRef)
		setOrders(data.docs.map((doc) => ({...doc.data(), id: doc.id})))
	}
	const handleDeleteButton = async (id) => {
		const orderDoc = doc(ordersCollectionRef, id)
		await deleteDoc(orderDoc)
		getOrders()
	}
	useEffect(() => {
		getOrders()
	}, [])
	return (
		<>
			<AdminHeader />
			<AdminSideBar />
			<div className="main-panel">
				<div className="content">
					<div className="container-fluid">
						<h4 className="page-title">Bakers Order</h4>
						<div className="row">
							<div className="col-md-12">
								<div className="card card-tasks">
									<div className="card-header ">
										<h4 className="card-title">
											Order List{" "}
											<Link
												to="/addOrder"
												className="btn btn-primary btn-sm float-right"
											>
												Add new Order
											</Link>{" "}
										</h4>
									</div>

									<div class="tab-content">
										<div className="card-body">
											<div className="table-full-width px-5 py-4 table-striped">
												<table className="table">
													<thead>
														<tr>
															<th>#</th>
															<th>
																Agent Name <sup>Agent Code</sup>
															</th>
															<th>Order Date</th>
															<th>Total Items</th>
															<th>Total Quantity</th>
															<th>Total Value</th>
															<th>Action</th>
														</tr>
													</thead>
													<tbody>
														{orders.map((order) => {
															return (
																<tr>
																	<td>{counter++}</td>
																	<td>
																		{order.agentName}{" "}
																		<sup>{order.agentCode}</sup>
																	</td>
																	<td>
																		{order.orderDate?.split(",")[0] ||
																			new Date(Date.now())
																				.toLocaleString()
																				.split(",")[0]}
																	</td>
																	<td>{order.orders.length}</td>
																	<td>₹{order.price || 4218}</td>
																	<td>{order.quantity || 59}</td>
																	<td className="td-actions">
																		<div className="form-button-action">
																			<Link to="/updateOrder">
																				<button
																					type="button"
																					className="btn btn-link btn-success"
																					onClick={() => {
																						localStorage.setItem(
																							"order_obj",
																							JSON.stringify(order)
																						)
																					}}
																				>
																					<i className="la la-edit"></i>
																				</button>
																			</Link>
																			<button
																				type="button"
																				onClick={() => {
																					handleDeleteButton(order.id)
																				}}
																				className="btn btn-link btn-danger"
																			>
																				<i className="la la-times"></i>
																			</button>
																		</div>
																	</td>
																</tr>
															)
														})}
													</tbody>
												</table>
											</div>
										</div>
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>
				<AdminFooter />
			</div>
		</>
	)
}
