import React, {useState} from "react"
import {useNavigate, Link} from "react-router-dom"
import AdminHeader from "./layouts/AdminHeader"
import AdminSideBar from "./layouts/AdminSideBar"
import AdminFooter from "./layouts/AdminFooter"
import {db} from "../firebase"
import {collection, doc, updateDoc} from "firebase/firestore"

export default function UpdateVendor() {
	const navigate = useNavigate()
	const categoryCollectionRef = collection(db, "zb_vendors")
	const [vendor, setVendor] = useState(
		JSON.parse(localStorage.getItem("vendor_obj"))
	)

	const [errorMsg, setErrorMsg] = useState("")
	const [successMsg, setSuccessMsg] = useState("")
	const handleUpdateVendor = async () => {
		if (vendor.name) {
			const categoryDoc = doc(categoryCollectionRef, vendor.id)
			await updateDoc(categoryDoc, vendor)
			setErrorMsg("")
			setSuccessMsg("Vendor updated successfully!")
			setTimeout(() => {
				setSuccessMsg("")
				navigate("/vendor")
			}, 1000)
		} else {
			setErrorMsg("Vendor name cannot be Empty!")
		}
	}
	return (
		<>
			<AdminHeader />
			<AdminSideBar />
			<div className="main-panel">
				<div className="content">
					<div className="container-fluid">
						<h4 className="page-title">Update Vendor</h4>
						<div className="row">
							<div className="col-md-12">
								<div className="card">
									<div className="card-header">
										<div className="card-title">
											Edit Vendor Details
											<Link
												to="/vendor"
												className="btn btn-danger btn-sm float-right"
											>
												Go BACK
											</Link>{" "}
										</div>
									</div>
									<div className="card-body px-4">
										<div className="form-group">
											<label htmlFor="username">Vendor Name</label>
											<input
												type="text"
												className="form-control"
												id="username"
												value={vendor.name}
												onChange={(event) =>
													setVendor((prev) => ({
														...prev,
														name: event.target.value,
													}))
												}
												placeholder="Enter Vendor Name"
											/>

											<div className="form-group">
												<label htmlFor="name">Location</label>
												<input
													type="text"
													className="form-control"
													value={vendor.Location}
													id="name"
													onChange={(event) =>
														setVendor((prev) => ({
															...prev,
															location: event.target.value,
														}))
													}
													placeholder="Enter Vendor Location"
												/>
											</div>
											<div className="form-group">
												<label htmlFor="name">Notes</label>
												<input
													type="text"
													className="form-control"
													value={vendor.notes}
													id="name"
													onChange={(event) =>
														setVendor((prev) => ({
															...prev,
															notes: event.target.value,
														}))
													}
													placeholder="Notes"
												/>
											</div>
										</div>
									</div>
									<div className="form-group px-4 mb-3">
										<div className="text-center text-danger">{errorMsg}</div>
										<div className="text-center text-success">{successMsg}</div>
										<button
											className="btn btn-success mx-3"
											onClick={handleUpdateVendor}
										>
											Update Vendor
										</button>
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>

				<AdminFooter />
			</div>
		</>
	)
}
