import React, {useState, useEffect} from "react"
import AdminHeader from "./layouts/AdminHeader"
import AdminSideBar from "./layouts/AdminSideBar"
import AdminFooter from "./layouts/AdminFooter"
import {Link} from "react-router-dom"
import {db} from "../firebase"
import {collection, getDocs, doc, deleteDoc} from "firebase/firestore"

export default function Vendor() {
	var counter = 1
	const [vendors, setVendors] = useState([])
	const vendorsCollectionRef = collection(db, "zb_vendors")
	const getTypes = async () => {
		const data = await getDocs(vendorsCollectionRef)
		setVendors(data.docs.map((doc) => ({...doc.data(), id: doc.id})))
	}
	const handleDeleteButton = async (id) => {
		const medDoc = doc(vendorsCollectionRef, id)
		await deleteDoc(medDoc)
		getTypes()
	}
	useEffect(() => {
		getTypes()
		console.log(getTypes())
	}, [])
	return (
		<>
			<AdminHeader />
			<AdminSideBar />
			<div className="main-panel">
				<div className="content">
					<div className="container-fluid">
						<h4 className="page-title">Vendor's List</h4>
						<div className="row">
							<div className="col-md-12">
								<div className="card card-tasks">
									<div className="card-header ">
										<h4 className="card-title">
											Vendors List{" "}
											<Link
												to="/addVendor"
												className="btn btn-primary btn-sm float-right"
											>
												Add new Vendor
											</Link>{" "}
										</h4>
									</div>

									<div className="card-body">
										<div className="table-full-width px-5 py-4 table-striped">
											<table className="table">
												<thead>
													<tr>
														<th>#</th>
														<th>
															VendorName<sup>Location</sup>
														</th>
														<th>Order No</th>
														<th>Date</th>
														<th>Total Amount</th>
														<th>Pending</th>
														<th>Action</th>
													</tr>
												</thead>
												<tbody>
													{vendors.map((vendor) => {
														return (
															<tr>
																<td>{counter++}</td>
																<td>
																	{vendor.name} <sup>{vendor.location}</sup>
																</td>
																<td>{vendor.notes}</td>
																<td>{vendor.type}</td>
																<td>₹{vendor.price}</td>
																<td>{vendor.stock}</td>
																<td className="td-actions">
																	<div className="form-button-action">
																		<Link to="/updateVendor">
																			<button
																				type="button"
																				className="btn btn-link btn-success"
																				onClick={() => {
																					localStorage.setItem(
																						"vendor_obj",
																						JSON.stringify(vendor)
																					)
																				}}
																			>
																				<i className="la la-edit"></i>
																			</button>
																		</Link>
																		<button
																			type="button"
																			onClick={() => {
																				handleDeleteButton(vendor.id)
																			}}
																			className="btn btn-link btn-danger"
																		>
																			<i className="la la-times"></i>
																		</button>
																	</div>
																</td>
															</tr>
														)
													})}
												</tbody>
											</table>
										</div>
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>
				<AdminFooter />
			</div>
		</>
	)
}
