import React, {useState, useEffect} from "react"
import {useNavigate, Link} from "react-router-dom"
import AdminHeader from "./layouts/AdminHeader"
import AdminSideBar from "./layouts/AdminSideBar"
import AdminFooter from "./layouts/AdminFooter"
import {db} from "../firebase"
import {collection, addDoc, getDocs} from "firebase/firestore"
import {OverlayTrigger, Tooltip} from "react-bootstrap"

export default function AddOrder() {
	const navigate = useNavigate()
	const [categories, setCategories] = useState([])
	const categoriesCollectionReference = collection(db, "zb_categories")
	const getCategories = async () => {
		const data = await getDocs(categoriesCollectionReference)
		setCategories(data.docs.map((doc) => ({...doc.data(), id: doc.id})))
	}

	useEffect(() => {
		getCategories()
	}, [])
	const orderCollectionRef = collection(db, "zb_order")
	const [errorMsg, setErrorMsg] = useState("")
	const [successMsg, setSuccessMsg] = useState("")
	const [order, setOrder] = useState({})
	const handleCreateOrder = async () => {
		if (order.agentName && order.agentCode) {
			setErrorMsg("")
			console.log(
				JSON.stringify({
					agentName: order.agentName,
					agentCode: order.agentCode,
					orderDate: new Date(Date.now()).toLocaleString(),
					orders: orderedItem,
				})
			)
			await addDoc(orderCollectionRef, {
				agentName: order.agentName,
				agentCode: order.agentCode,
				orderDate: new Date(Date.now()).toLocaleString(),
				orders: orderedItem,
			})
			setSuccessMsg("Order created successfully!")
			setTimeout(() => {
				setSuccessMsg("")
				navigate("/order")
			}, 1000)
		} else {
			setErrorMsg("Please fill out all the required fields!")
		}
	}

	const [orderedItem, setOrderedItem] = useState([
		{item: "Bread", quantity: 18},
		{item: "PV", quantity: 20},
		{item: "DB", quantity: 100},
	])

	const handleAddRow = () => {
		setOrderedItem((prevRows) => [{item: "", quantity: ""}, ...prevRows])
	}

	const handleInputChange = (index, fieldName, value) => {
		setOrderedItem((prevRows) => {
			const newRows = [...prevRows]
			newRows[index][fieldName] = value
			return newRows
		})
	}

	const handleRemoveRow = (index) => {
		setOrderedItem((prevRows) => {
			const newRows = [...prevRows]
			newRows.splice(index, 1)
			return newRows
		})
	}

	return (
		<>
			<AdminHeader />
			<AdminSideBar />
			<div className="main-panel">
				<div className="content">
					<div className="container-fluid">
						<h4 className="page-title">Add Order :</h4>
						<div className="row">
							<div className="col-md-12">
								<div className="card">
									<div className="card-header">
										<div className="card-title">
											Order Details
											<Link
												to="/inventory"
												className="btn btn-danger btn-sm float-right"
											>
												Go BACK
											</Link>{" "}
											<div className="form-group row">
												<div className="col">
													<label htmlFor="stock">Agent Code</label>
													<input
														type="text"
														className="form-control"
														id="agentCode"
														value={order.agentCode}
														onChange={(event) =>
															setOrder((prev) => ({
																...prev,
																agentCode: event.target.value,
															}))
														}
														placeholder="Enter Agent Code"
													/>
												</div>
												<div className="col">
													<label htmlFor="stock">Agent Name</label>
													<input
														type="text"
														className="form-control"
														id="agentName"
														value={order.agentName}
														onChange={(event) =>
															setOrder((prev) => ({
																...prev,
																agentName: event.target.value,
															}))
														}
														placeholder="Enter Agent Name"
													/>
												</div>
											</div>
										</div>
									</div>
									<div className="card-body px-4">
										<div className="d-flex justify-content-end">
											<button
												className="btn btn-primary"
												onClick={handleAddRow}
											>
												Add Items
											</button>
										</div>
										{orderedItem.map((row, index) => (
											<div className="form-group row" key={index}>
												<div className="col">
													<label htmlFor={`item-${index}`}>ItemName</label>

													<select
														class="form-control input-pill"
														onChange={(event) =>
															handleInputChange(
																index,
																"item",
																event.target.value
															)
														}
														id="exampleFormControlSelect1"
													>
														<option value="">Select an item...</option>
														{categories.map((category) => {
															return (
																<option value={category.name}>
																	{category.name}
																</option>
															)
														})}
													</select>
												</div>
												<div className="col">
													<label htmlFor={`quantity-${index}`}>Quantity</label>
													<input
														type="number"
														className="form-control input-pill"
														id={`quantity-${index}`}
														value={row.quantity}
														onChange={(event) =>
															handleInputChange(
																index,
																"quantity",
																event.target.value
															)
														}
														placeholder="Quantity"
													/>
												</div>
												<div className="col-auto">
													<OverlayTrigger
														placement="top"
														overlay={
															<Tooltip id={`tooltip-close-${index}`}>
																Close
															</Tooltip>
														}
													>
														<button
															type="button"
															className="btn btn-link close-icon"
															onClick={() => handleRemoveRow(index)}
														>
															&times;
														</button>
													</OverlayTrigger>
												</div>
											</div>
										))}
									</div>

									<div className="form-group px-4 mb-3">
										<div className="text-center text-danger">{errorMsg}</div>
										<div className="text-center text-success">{successMsg}</div>
										<button
											className="btn btn-primary mx-3"
											onClick={handleCreateOrder}
										>
											Create Order
										</button>
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>

				<AdminFooter />
			</div>
		</>
	)
}
