import React, { useState, useEffect } from "react";
import AdminHeader from "./layouts/AdminHeader";
import AdminSideBar from "./layouts/AdminSideBar";
import AdminFooter from "./layouts/AdminFooter";
import { Link } from "react-router-dom";
import { db } from "../firebase";
import { collection, getDocs, doc, deleteDoc } from "firebase/firestore";

export default function Inventory() {
  var counter = 1;
  const [medicines, setMedicines] = useState([]);
  const medicinesCollectionRef = collection(db, "zb_inventory")
	const getTypes = async () => {
		const data = await getDocs(medicinesCollectionRef)
		setMedicines(data.docs.map((doc) => ({...doc.data(), id: doc.id})))
	}
	const handleDeleteButton = async (id) => {
		const medDoc = doc(medicinesCollectionRef, id)
		await deleteDoc(medDoc)
		getTypes()
	}
	useEffect(() => {
		getTypes()
	}, [])
	return (
		<>
			<AdminHeader />
			<AdminSideBar />
			<div className="main-panel">
				<div className="content">
					<div className="container-fluid">
						<h4 className="page-title">Bakers Inventory</h4>
						<div className="row">
							<div className="col-md-12">
								<div className="card card-tasks">
									<div className="card-header ">
										<h4 className="card-title">
											Inventory List{" "}
											<Link
												to="/addinventory"
												className="btn btn-primary btn-sm float-right"
											>
												Add Inventory
											</Link>{" "}
										</h4>
									</div>
									<nav>
										<div
											class="nav nav-tabs nav-fill"
											id="nav-tab"
											role="tablist"
										>
											<a
												class="nav-item nav-link active"
												id="nav-home-tab"
												data-toggle="tab"
												href="#nav-home"
												role="tab"
											>
												ALL
											</a>
											<a
												class="nav-item nav-link"
												id="nav-profile-tab"
												data-toggle="tab"
												href="#nav-profile"
												role="tab"
											>
												SuperBread
											</a>
											<a
												class="nav-item nav-link"
												id="nav-contact-tab"
												data-toggle="tab"
												href="#nav-contact"
												role="tab"
											>
												Diamond
											</a>
										</div>
									</nav>
									<div class="tab-content" id="nav-tabContent">
										<div className="card-body" id="nav-contact" role="tabpanel">
											<div className="table-full-width px-5 py-4 table-striped">
												<table className="table">
													<thead>
														<tr>
															<th>#</th>
															<th>
																Item Name - Diamond<sup>Weight</sup>
															</th>
															<th>Item Category</th>
															<th>Item Type</th>
															<th>Item Price</th>
															<th>Stock</th>
															<th>Action</th>
														</tr>
													</thead>
													<tbody>
														{medicines.map((item) => {
															return (
																<tr>
																	<td>{counter++}</td>
																	<td>
																		{item.name} <sup>{item.power}</sup>
																	</td>
																	<td>{item.category}</td>
																	<td>{item.type}</td>
																	<td>₹{item.price}</td>
																	<td>{item.stock}</td>
																	<td className="td-actions">
																		<div className="form-button-action">
																			<Link to="/updateinventory">
																				<button
																					type="button"
																					className="btn btn-link btn-success"
																					onClick={() => {
																						localStorage.setItem(
																							"inventory_obj",
																							JSON.stringify(item)
																						)
																					}}
																				>
																					<i className="la la-edit"></i>
																				</button>
																			</Link>
																			<button
																				type="button"
																				onClick={() => {
																					handleDeleteButton(item.id)
																				}}
																				className="btn btn-link btn-danger"
																			>
																				<i className="la la-times"></i>
																			</button>
																		</div>
																	</td>
																</tr>
															)
														})}
													</tbody>
												</table>
											</div>
										</div>
										<div className="card-body" id="nav-profile" role="tabpanel">
											<div className="table-full-width px-5 py-4 table-striped">
												<table className="table">
													<thead>
														<tr>
															<th>#</th>
															<th>
																Item Name - SuperBread<sup>Weight</sup>
															</th>
															<th>Item Category</th>
															<th>Item Type</th>
															<th>Item Price</th>
															<th>Stock</th>
															<th>Action</th>
														</tr>
													</thead>
													<tbody>
														{medicines.map((item) => {
															return (
																<tr>
																	<td>{counter++}</td>
																	<td>
																		{item.name} <sup>{item.power}</sup>
																	</td>
																	<td>{item.category}</td>
																	<td>{item.type}</td>
																	<td>₹{item.price}</td>
																	<td>{item.stock}</td>
																	<td className="td-actions">
																		<div className="form-button-action">
																			<Link to="/updateinventory">
																				<button
																					type="button"
																					className="btn btn-link btn-success"
																					onClick={() => {
																						localStorage.setItem(
																							"inventory_obj",
																							JSON.stringify(item)
																						)
																					}}
																				>
																					<i className="la la-edit"></i>
																				</button>
																			</Link>
																			<button
																				type="button"
																				onClick={() => {
																					handleDeleteButton(item.id)
																				}}
																				className="btn btn-link btn-danger"
																			>
																				<i className="la la-times"></i>
																			</button>
																		</div>
																	</td>
																</tr>
															)
														})}
													</tbody>
												</table>
											</div>
										</div>
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>
				<AdminFooter />
			</div>
		</>
	)
}
