import React from "react";
import "./App.css";
import { BrowserRouter, Routes, Route } from "react-router-dom";
import Dashboard from "./components/Dashboard";
import AdminLogin from "./components/AdminLogin";
import AdminRegister from "./components/AdminRegister";
import AdminProfile from "./components/AdminProfile";
import Category from "./components/Category"
import AddCategory from "./components/AddCategory"
import UpdateCategory from "./components/UpdateCategory"
import ItemTypes from "./components/ItemTypes"
import AddType from "./components/AddType"
import UpdateType from "./components/UpdateType"
import Inventory from "./components/Inventory"
import AddInventory from "./components/AddInventory"
import UpdateInventory from "./components/UpdateInventory"
import Vendor from "./components/Vendor"
import Customer from "./components/Customer"
import AddVendor from "./components/AddVendor"
import UpdateVendor from "./components/UpdateVendor"
import AddOrder from "./components/AddOrder"
import Order from "./components/Order"
import UpdateOrder from "./components/UpdateOrder"

function App() {
	return (
		<BrowserRouter>
			<Routes>
				<Route exact path="/" element={<Dashboard />} />
				<Route exact path="/login" element={<AdminLogin />} />

				<Route exact path="/register" element={<AdminRegister />} />
				<Route exact path="/profile" element={<AdminProfile />} />

				<Route exact path="/categories" element={<Category />} />
				<Route exact path="/addCategory" element={<AddCategory />} />
				<Route exact path="/updatecategory" element={<UpdateCategory />} />

				<Route exact path="/types" element={<ItemTypes />} />
				<Route exact path="/addtype" element={<AddType />} />
				<Route exact path="/updatetype" element={<UpdateType />} />

				<Route exact path="/inventory" element={<Inventory />} />
				<Route exact path="/addinventory" element={<AddInventory />} />
				<Route exact path="/updateInventory" element={<UpdateInventory />} />

				<Route exact path="/customer" element={<Customer />} />

				<Route exact path="/vendor" element={<Vendor />} />
				<Route exact path="/addVendor" element={<AddVendor />} />
				<Route exact path="/updateVendor" element={<UpdateVendor />} />

				<Route exact path="/order" element={<Order />} />
				<Route exact path="/addOrder" element={<AddOrder />} />
				<Route exact path="/updateOrder" element={<UpdateOrder />} />
			</Routes>
		</BrowserRouter>
	)
}

export default App;

