import React, {useState} from "react"
import {useNavigate, Link} from "react-router-dom"
import AdminHeader from "./layouts/AdminHeader"
import AdminSideBar from "./layouts/AdminSideBar"
import AdminFooter from "./layouts/AdminFooter"
import {db} from "../firebase"
import {collection, addDoc} from "firebase/firestore"

export default function AddVendor() {
	const navigate = useNavigate()

	const vendorCollectionRef = collection(db, "zb_vendors")
	const [errorMsg, setErrorMsg] = useState("")
	const [successMsg, setSuccessMsg] = useState("")
	const [vendorName, setVendorName] = useState("")
	const [vendorLocation, setVendorLocation] = useState("")

	const handleAddVendor = async () => {
		if (vendorName) {
			setErrorMsg("")
			await addDoc(vendorCollectionRef, {
				name: vendorName,
				location: vendorLocation,
				notes: "",
			})
			setSuccessMsg("Vendor added successfully!")
			setTimeout(() => {
				setSuccessMsg("")
				navigate("/vendor")
			}, 1000)
		} else {
			setErrorMsg("Vendor name required!")
		}
	}
	return (
		<>
			<AdminHeader />
			<AdminSideBar />
			<div className="main-panel">
				<div className="content">
					<div className="container-fluid">
						<h4 className="page-title">Create Vendor</h4>
						<div className="row">
							<div className="col-md-12">
								<div className="card">
									<div className="card-header">
										<div className="card-title">
											New Vendor Details
											<Link
												to="/vendor"
												className="btn btn-danger btn-sm float-right"
											>
												Go BACK
											</Link>{" "}
										</div>
									</div>
									<div className="card-body px-4">
										<div className="form-group">
											<label htmlFor="name">Vendor Name</label>
											<input
												type="text"
												className="form-control"
												value={vendorName}
												id="name"
												onChange={(event) => {
													setVendorName(event.target.value)
												}}
												placeholder="Enter Vendor Name"
											/>
										</div>
										<div className="form-group">
											<label htmlFor="name">Location</label>
											<input
												type="text"
												className="form-control"
												value={vendorLocation}
												id="name"
												onChange={(event) => {
													setVendorLocation(event.target.value)
												}}
												placeholder="Enter Vendor Location"
											/>
										</div>
										<div className="form-group">
											<label htmlFor="name">Notes</label>
											<input
												type="text"
												className="form-control"
												value={vendorLocation}
												id="name"
												onChange={(event) => {
													setVendorLocation(event.target.value)
												}}
												placeholder="Notes"
											/>
										</div>
									</div>
									<div className="form-group px-4 mb-3">
										<div className="text-center text-danger">{errorMsg}</div>
										<div className="text-center text-success">{successMsg}</div>
										<button
											className="btn btn-primary mx-3"
											onClick={handleAddVendor}
										>
											Add Vendor
										</button>
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>

				<AdminFooter />
			</div>
		</>
	)
}
